<template>
  <div class="container mt-5">
    <form class="row g-1 d-flex justify-content-center">
      <div class="mb-3 col-sm-7">
        <label for="exampleInputEmail1" class="form-label">Email address</label>
        <input
          type="email"
          class="form-control"
          id="exampleInputEmail1"
          aria-describedby="emailHelp"
          required
          autocomplete="username"
        />
        <div id="emailHelp" class="form-text">
          We'll never share your email with anyone else.
        </div>
      </div>
      <div class="mb-3 col-sm-7">
        <label for="exampleInputPassword1" class="form-label">Password</label>
        <input
          type="password"
          class="form-control"
          id="exampleInputPassword1"
          required
          autocomplete="current-password"
        />
      </div>

      <div class="mb-3 col-md-6 text-center">
        <button type="submit" class="btn btn-primary" formaction="/">Submit</button>
      </div>
      <div class="mb-3 col-md-6 text-center">

        <button type="button" class="btn btn-secondary"  >
          <a href="/#/formregister" style=" text-decoration: none; color: white;">Register</a>
        </button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "Form",
  data() {
    return {

      msg: "mensaje"
    }
  },
};
</script>



