<template>
  <div class="container">
    <h2> Recomendadas </h2>
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      class="class-name"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <b-carousel-slide
        caption="First slide"
        text="Nulla vitae elit libero, a pharetra augue mollis interdum."
        class="class-name"
      >

      </b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide class="class-name"> </b-carousel-slide>

      <!-- Slides with image only -->
      <b-carousel-slide class="class-name"></b-carousel-slide>


    </b-carousel>

  </div>
</template>

<script>
export default {
  data() {
    return {
      slide: 0,
      sliding: null,
    };
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
  },
};
</script>

<style>
.class-name {
  height: 20vh;
}
</style>



