<template>
  <div class="bg-warning bg-gradient sticky-top pt-4" style="height: 150px;">
    <h2>{{ titulo }}</h2>
    <ul class="list-group list-group-horizontal d-flex justify-content-center">
      <li
        v-for="opcion in opciones"
        :key="opcion.id"
        class="list-group-item justify-center"

      >

        <a v-bind:href="opcion.href"
          style="text-decoration: none;"
        > {{ opcion.titulo }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      msg: "mensaje",
      titulo: "CAC-Western Movies",
      opciones: [
        { id: 1, titulo: "Tendencias", href: "/" },
        { id: 2, titulo: "Registrarse", href: "/#/formregister" },
        { id: 3, titulo: "Iniciar Sesión", href: "/#/form" },
      ],
    };
  },
};
</script>



