import Vue from "vue";
import Router from "vue-router";
import Header from "@/components/Header";
import Main from "@/components/Main";
import SearchBar from "@/components/SearchBar";
import Form from "@/components/Form";
import FormRegister from "@/components/FormRegister";
import Footer from "@/components/Footer"
import ButtonUp from "@/components/ButtonUp"
import WallpaperMovie from "@/components/WallpaperMovie"
import CarouselMovies from "@/components/CarouselMovies"

// carousel
import { CarouselPlugin, BCarousel } from 'bootstrap-vue'

import AOS from "aos";
import "aos/dist/aos.css";

Vue.use(Router);


Vue.component('b-carousel', BCarousel)

Vue.component("app-header", Header);
Vue.component("app-main", Main);
Vue.component("app-searchbar", SearchBar);
Vue.component("app-footer", Footer)
Vue.component("app-button-up", ButtonUp)
Vue.component("app-wallpaper-movie", WallpaperMovie)
Vue.component("app-carousel-movies",CarouselMovies)


Vue.use(CarouselPlugin)
AOS.init();

// prueba cambio

export default new Router({
  routes: [
    {
      path: "/",
      name: "Main",
      component: Main,
    },
    {
      path: "/form",
      name: "Form",
      component: Form,
    },
    {
      path: "/formregister",
      name: "FormRegister",
      component: FormRegister
    }
  ],
});



