<template>
  <div>
    <app-wallpaper-movie v-if="info != null"/>

    <div class="container">
      <div>
        <app-searchbar
          v-if="info != null && info != 'Error 500' && info != 'Error Catch'"
        />
        <!-- <div id="liveAlertPlaceholder"></div>
      <button
        type="button"
        class="btn btn-primary"
        id="liveAlertBtn"
        @click="alert"
      >
        Show live alert
      </button> -->
      </div>

      <!-- Mensajes cargando y Error -->

      <h2 v-if="info == null" class="mt-3">
        <div class="spinner-border" role="status">
          <span class="visually-hidden">Loading...</span>
        </div>
        Cargando...
      </h2>

      <h2 v-if="info == 'Error 500'" class="text-warning mt-3">
        <div class="alert alert-warning" role="alert">
          ⚠️Error al cargar API vuelva a intentarlo..
        </div>
      </h2>

      <h2 v-if="info == 'Error Catch'" class="mt-3 text-danger h2">
        <div class="alert alert-danger" role="alert">
          {{ info }}
        </div>
      </h2>

      <!-- --- -->

      <div
        v-if="info != 'Error 500' && info != null && info != 'Error Catch'"
        class="d-flex flex-wrap justify-content-center"
      >
        <div
          class="card mt-2"
          v-for="i in info"
          :key="i.id"
          style="width: 18rem"
          v-if="
            i.posterURL !=
              'https://m.media-amazon.com/images/M/MV5BZDYxMTQzNjgtZDJkMC00OGYwLTliYjgtNDA2ZjBiMmUxYmY4XkEyXkFqcGdeQXVyNjc1NTYyMjg@._V1_SX300.jpg' &&
            i.posterURL !=
              'https://m.media-amazon.com/images/M/MV5BZGI5MjBmYzYtMzJhZi00NGI1LTk3MzItYjBjMzcxM2U3MDdiXkEyXkFqcGdeQXVyNzkwMjQ5NzM@._V1_SX300.jpg'
          "
        >
          <img :src="i.posterURL" alt="" class="card-img-top" />
          <div class="card-body">
            <h5 class="card-title">{{ i.title }}</h5>
          </div>
        </div>
      </div>

      <!-- <ul class="list-group">
      <li v-for="i in info" :key="i.id" class="list-group-item">
        <h2>{{ i.title }}</h2>
        <img :src="i.posterURL" alt="" />
      </li>
    </ul> -->
    </div>

    <!-- Carousel -->

    <div
      v-if="info != null && info != 'Error 500' && info != 'Error Catch'"
      class="container mt-5"
    >
      <hr />
      <h2>Recomendadas</h2>
      <b-carousel
        id="carousel-1"
        v-model="slide"
        :interval="2000"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
        v-if="info != null"
      >
        <!-- Slides with custom text -->
        <b-carousel-slide>
          <template #img>
            <img
              class="d-block img-fluid w-50 mx-auto"
              :src="info[13].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <template #img>
            <img
              class="d-block img-fluid w-50 mx-auto"
              :src="info[15].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <template #img>
            <img
              class="d-block img-fluid w-50 mx-auto"
              :src="info[0].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>
        <!-- Slides with image only -->
      </b-carousel>
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
export default {
  name: "Main",
  data() {
    return {
      info: null,
      slide: 0,
      sliding: null,
    };
  },
  methods: {
    fetchData() {
      fetch("https://api.sampleapis.com/movies/western")
        .then((response) => {
          // status ok url 200
          console.log(`Status: ${response.status}`);
          console.log(`Response: ${JSON.stringify(response)}`);
          console.log(response);
          console.log(JSON.stringify(response));
          if (response.ok) {
            console.log("response OK");
            response.json().then((data) => {
              console.log("Data:");
              console.log(data);
              if (data.error == 500) {
                console.log("error 500");
                // throw new Error("Error status 500")
                this.info = "Error 500";
              } else {
                console.log("Finalizamos fetch ok");
                this.info = data;
              }
            });
          } else {
            console.log("error status");
            throw new Error("Not 200 ok");
          }
        })
        .catch((err) => {
          console.log("error del catch");
          console.log(err);
          alert(`Error al cargar la API (vuelva a intentar): ${err}`);
          this.info = `Error Catch`;
        });
    },
    alert() {
      const alertPlaceholder = document.getElementById("liveAlertPlaceholder");
      const appendAlert = (message, type) => {
        const wrapper = document.createElement("div");
        wrapper.innerHTML = [
          `<div class="alert alert-${type} alert-dismissible" role="alert">`,
          `   <div>${message}</div>`,
          '   <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>',
          "</div>",
        ].join("");

        alertPlaceholder.append(wrapper);
      };

      const alertTrigger = document.getElementById("liveAlertBtn");
      if (alertTrigger) {
        alertTrigger.addEventListener("click", () => {
          appendAlert("Nice, you triggered this alert message!", "success");
        });
      }
    },
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
  },
  mounted() {
    this.fetchData();
    // axios
    //   .get('https://api.coindesk.com/v1/bpi/currentprice.json')
    //   .then(response => (this.info = response))
    //   .catch(err => (this.info = "Error papu"))
  },
};
</script>

<style>
.class-name {
  height: 400px;
}
</style>



